import {f7} from "framework7-react";
import {CrossEvent, CrossMember} from "./CrossMember";

const maxRetryCount = 5;
export class TbMember{
  trackingUuid = ""
  constructor(onSuccess, onError, onComplete){
    this.onSuccess = onSuccess;
    this.onError = onError;
    this.onComplete = onComplete;
    this.retryCount = 0;
    this.member = null;
    // this.send = this.send.bind(this);
    // this.canRetry = this.canRetry.bind(this);
    // this.retry = this.retry.bind(this);
  }
  send(params){
    let header = {
      'content-type': 'application/x-www-form-urlencoded',
    };

    let member = this.member = new CrossMember();
    member.addEventListener(CrossEvent.EventTypes.onError, this._onError);
    member.addEventListener(CrossEvent.EventTypes.onSuccess, this._onSuccess);
    member.addEventListener(CrossEvent.EventTypes.onComplete, this._onComplete);
    // member.load(CrossMember.Methods.post, "https://eco.taobao.com/router/rest", params, header);
    // member.load(CrossMember.Methods.post, "http://gw.api.taobao.com/router/rest", params, header);
    member.load(CrossMember.Methods.post, "//gw.api.taobao.com/router/rest", params, header);
  }
  retry(){
    this.retryCount ++;
    this.send();
  }
  canRetry(){
    return this.retryCount < maxRetryCount;
  }

  _onError = (e)=>{
    if(this.onError){
      this.onError(e);
    }
  }
  _onSuccess = (e)=>{
    let res = e.res;
    if(!res || (res.errMsg != "request:ok") || !res.data){
      if(this.onError){
        this.onError(res);
      }
      return f7.toast.create({closeTimeout: 3000,text:'订单侠淘宝没有返回数据', destroyOnClose:true}).open();
    }

    let data = res.data;
    if(typeof this.onSuccess == 'function'){
      this.onSuccess(data);
    }
  }
  _onComplete = (e)=>{
    this.isConnecting = false;
    if(this.onComplete){
      this.onComplete();
    }
  }
}
// module.exports = TbMember;